body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
  font-family: "Fira Code", monospace;
}

* {
  box-sizing: border-box;
}

#root, body, html {
  width: 100%;
  height: 100%;
}

fieldset {
  border: 0;
}

p {
  margin: '0'
}

button, input {
  font-family: inherit;
  font-weight: inherit;
}
